<template>
  <div id="nav" class="nav" :class="{'pos': showMenu}">
    <div class="nav-wrapper">
      <div class="nav-left">
        <img v-if="showMenuButton" @click="showMenuPanel()" src="@/assets/image/common/more_icon.png" width="26" height="19" alt="">
        <van-icon v-else @click="$router.back()" name="arrow-left" size="19" />
      </div>
      <div class="nav-center">{{ title }}</div>
      <div class="nav-right" v-if="showNavRight">
        <div v-if="!isLogin">
          <van-button type="primary" @click="login()">登录</van-button>
          <van-button type="info" @click="register()">注册</van-button>
        </div>
        <div v-else class="user-icon" @click="closeMenu(false)">
          <van-dropdown-menu>
            <van-dropdown-item ref="item">
              <template #title>
                <img src="@/assets/image/common/headportrait.png" width="19" height="19" alt="">
                <span @>回头是岸</span>
              </template>
              <div @click="goPage('Profile')">
                个人信息
              </div>
              <div @click="logOut()">
                退出登录
              </div>
            </van-dropdown-item>
          </van-dropdown-menu>
        </div>
      </div>
    </div>
    <Menu
      @closeMenu="closeMenu($event)"
      :tempShowMenu="showMenu"
      :activeMenuNumber="activeMenuNumber"
      @showTutorPop="showTutorPop()"
      :posNav="posNav" />
    <Login @closeLogin="closeLogin($event)" :showLogin="showLogin" />
    <Register @closeRegister="closeRegister($event)" :showRegister="showRegister" />
    <Tutor @closeTutor="closeTutor($event)" :showTutor="showTutor" />
  </div>
</template>

<script>
import Menu from '@/components/common/Menu.vue'
import Login from '@/components/Login.vue'
import Register from '@/components/Register.vue'
import Tutor from '@/components/Tutor.vue'

export default {
  name: 'Nav',
  components: {
    Menu,
    Login,
    Register,
    Tutor
  },
  data() {
    return {
      showMenu: false,
      posNav: 44,
      showLogin: false,
      showRegister: false,
      showTutor: false,
    };
  },
  props: {
    title: {
      type: String,
      default: '',
    },
    activeMenuNumber: {
      type: Number,
      default: 0,
    },
    showMenuButton: {
      type: Boolean,
      default: true,
    },
    showNavRight: {
      type: Boolean,
      default: true,
    },
  },
  computed: {
    isLogin() {
      return this.$store.state.isLogin;
    }
  },
  methods: {
    login() {
      this.closeMenu(false);
      this.showLogin = true;
    },
    logOut() {
      this.$store.dispatch("setLoginStatus", false);
      this.goPage('Home');
    },
    closeLogin(val) {
      this.showLogin = val;
    },
    register() {
      this.closeMenu(false);
      this.showRegister = true;
    },
    closeRegister(val) {
      this.showRegister = val;
    },
    showTutorPop() {
      this.closeMenu(false);
      this.showTutor = true;
    },
    closeTutor(val) {
      this.showTutor = val;
    },
    goPage(url) {
      if (this.$route.name == url) return;
      else {
        this.$router.push({ name: url });
      }
    },
    showUserPanel() {
      let _item = this.$refs.item;
      _item.toggle();
    },
    showMenuPanel() {
      this.showMenu = !this.showMenu;
      let position = document.querySelector('#nav');
      let topNavPos = position.getBoundingClientRect().top;
      let topNavH = position.getBoundingClientRect().height;
      this.posNav = topNavH;
      this.posNav = this.posNav - Math.abs(topNavPos);
    },
    closeMenu(val) {
      this.showMenu = val;
    }
  },
}
</script>

<style lang="less">
@import "../../assets/css/common/nav.less";
</style>
