<template>
  <van-popup
    v-model="showMenu"
    class="menu"
    position="left"
    :lazy-render="true"
    :safe-area-inset-bottom="true"
    :get-container="getContainer"
    :close-on-click-overlay="true"
    @closed="closeMenu"
    :overlay-style="{
      marginTop: `${posNav}px`,
    }"
    :style="{
      height: 'auto',
      top: '176px',
      marginTop: `${posNav}px`,
      width: '120px'
    }">
    <ul>
      <li
        v-for="(menuItem, menuKey) in menuList"
        :key="menuKey"
        :class="{ 'active': menuKey === activeMenuNumber }"
        @click="goPage(menuItem.link)">
        <span>{{ menuItem.title }}</span>
      </li>
    </ul>
  </van-popup>
</template>

<script>
export default {
  name: 'Menu',
  data() {
    return {
      showMenu: false,
      menuList: [
        { title: '首页', link: 'Home' },
        { title: '戒友圈', link: 'Forum' },
        { title: '每日签到', link: 'DailyCheckIn' },
        { title: '戒赌', link: 'QuitGambling' },
        { title: '债务', link: 'Debt' },
        { title: '导航', link: 'Navigation' },
        { title: '心是树洞', link: 'ThreeHole' },
        { title: '聆听者', link: '' },
      ],
    };
  },
  methods: {
    getContainer() {
      return document.querySelector('#app');
    },
    closeMenu() {
      this.$emit('closeMenu', false)
    },
    showTutorPop() {
      this.$emit('showTutorPop')
    },
    goPage(url) {
      if (url) {
        if (this.$route.name == url) return;
        else {
          this.$router.push({ name: url });
        }
      } else {
        this.showTutorPop()
      }
    },
  },
  props: {
    tempShowMenu: {
      type: Boolean,
      default: false
    },
    activeMenuNumber: {
      type: Number,
      default: 0,
    },
    posNav: {
      type: Number,
      default: 44,
    },
  },
  watch: {
    tempShowMenu: function(newVal, oldVal) {
      this.showMenu = newVal;
    }
  },
}
</script>

<style lang="less">
@import "../../assets/css/common/menu.less";
</style>