<template>
  <van-popup
    class="crendential login"
    v-model="showLogin"
    :close-on-click-overlay="false"
    :get-container="getContainer">
    <div class="header">
      欢迎回到戒友大家庭
      <img @click="closeLogin()" width="23" height="23" src="@/assets/image/credentials/close_icon.png" alt="">
    </div>
    <div class="body">
      <van-form @submit="onSubmit" label-width="54">
        <van-field
          v-model="username"
          name="Username"
          label="用户名："
          placeholder="请输入您的用户名"
          :rules="[{ required: true, message: '用户名由3-15个字符组成' }]"
        />
        <van-field
          v-model="password"
          type="password"
          name="Password"
          label="密码："
          placeholder="请输入账户密码"
          :rules="[{ required: true, message: '密码包含3-15个字符' }]"
        />
        <van-field
          class="code-input"
          v-model="code"
          name="Code"
          label="验证码："
          placeholder="请输入图形验证码"
          :rules="[{ required: true, message: '请输入图形验证码' }]"
        />
        <div class="code">
          <img width="51" height="16" src="" alt="">
          <span>换一个</span>
        </div>
        <div class="van-cell btn">
          <van-button round block type="info" native-type="submit">
            立即注册
          </van-button>
        </div>
      </van-form>
    </div>
  </van-popup>
</template>

<script>
export default {
  name: 'Login',
  data() {
    return {
      username: '',
      password: '',
      code: '',
    };
  },
  methods: {
    getContainer() {
      return document.querySelector('.main');
    },
    closeLogin() {
      this.$emit('closeLogin', false)
    },
    onSubmit(values) {
      this.$emit('closeLogin', false)
      this.$store.dispatch("setLoginStatus", true);
    },
  },
  props: {
    showLogin: {
      type: Boolean,
      default: false
    }
  }
}
</script>

<style lang="less">
.login {
  max-width: 338px;
  width: 90%;
  padding: 10px;

  .header {
    position: relative;
    height: 70px;
    line-height: 70px;
    font-size: 18px;
    color: #333333;
    font-weight: 600;
    text-align: left;
    padding-left: 77px;
  }

  .header img {
    position: absolute;
    right: 0;
    top: 0;
  }

  .van-form {
    padding: 0px 11px;
  }
  .van-form .van-cell {
    padding: 0;
    min-height: 40px;
    margin-bottom: 15px;
  }

  .van-form .van-cell::after {
    content: none;
  }

  .van-form .van-field__control {
    width: 160px;
    height: 26px;
    background-color: #ffffff;
    color: #666666;
    border-radius: 3px;
    border: solid 1px #bfbfbf;
    font-size: 12px;
    padding: 0px 15px;
  }

  .van-form .van-field__control::placeholder {
    color: #d9d9d9;
  }

  .van-form .van-field__label {
    font-size: 13px;
    color: #666666;
    word-break: keep-all;
    text-align: right;
  }

  .van-form .van-field__error-message {
    font-size: 9px;
    line-height: 14px;
    color: #fa4444;
  }

  .van-form .code-input {
    margin-bottom: 0px;
  }

  .van-form .code {
    padding-left: 66px;
    display: flex;
    align-items: center;
    margin-bottom: 15px;
    font-size: 9px;
    line-height: 24px;
    letter-spacing: 0px;
    color: #4486fa;
  }

  .van-form .code img {
    margin-right: 7px;
  }

  .van-form .van-button {
    text-align: center;
    width: 160px;
    height: 26px;
    background-color: #4486fa;
    border-radius: 3px;
  }

  .van-form .btn {
    padding-left: 66px;
  }

}
</style>